<template>
  <div>
    <v-row v-if="loggedAs === 'school'">
      <v-col cols="2">
        <v-select
          label="Course"
          outlined
          item-text="name"
          item-value="id"
          v-model="selectedCourse"
          :items="courses"
        ></v-select>
      </v-col>
      <v-col cols="2">
        <v-select
          label="Teacher"
          outlined
          :item-text="
            item => item.firstName + ' ' + item.middleName + ' ' + item.lastName
          "
          item-value="id"
          v-model="selectedTeacher"
          :items="teachers"
        >
        </v-select>
      </v-col>
      <v-col cols="2">
        <v-select
          label="Semester"
          outlined
          v-model="selectedSemester"
          :items="semesters"
        ></v-select>
      </v-col>
      <v-col cols="2">
        <v-textarea
          label="Code"
          outlined
          rows="1"
          no-resize
          v-model="offerCode"
        ></v-textarea>
      </v-col>
      <v-col cols="2">
        <v-btn color="accent" @click="create">New Offer</v-btn>
      </v-col>
    </v-row>

    <v-card outlined raised>
      <v-data-table
        :headers="headers"
        :items="offers"
        class="elevation-1"
        @click:row="goToOfferProfile"
      >
        <template v-slot:item.action="{ item }">
          <v-icon @click="deleteItem(item)" color="error">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import OfferService from "../services/OfferService";
import TeacherService from "../services/TeacherService";
import CourseService from "../services/CourseService";

export default {
  data() {
    return {
      offers: [],
      teachers: [],
      courses: [],
      semesters: ["Winter", "Spring", "Summer", "Fall"],
      selectedSemester: "",
      selectedTeacher: "",
      selectedCourse: "",
      offerCode: "",
      headers: [
        {
          text: "Course",
          value: "course"
        },
        { text: "Code", value: "code" },
        { text: "Teacher", value: "firstName" },
        { text: "Semester", value: "semester" },
        { text: "Actions", value: "action", sortable: false }
      ],
      inputName: ""
    };
  },
  created() {
    if (this.$store.state.loggedAs == "teacher") {
      OfferService.GetOffers().then(offers => {
        for (let i = 0; i < offers.length; i++) {
          const el = offers[i];
          CourseService.GetCourseById(el.courseId).then(course => {
            el.course = course.name;
            this.offers.splice(i, 1, el);
          });
        }
      });
      this.headers.splice(2, 1);
    } else {
      OfferService.GetOffers().then(offers => {
        this.offers = offers;
        for (let i = 0; i < offers.length; i++) {
          const el = offers[i];

          TeacherService.GetTeacherById(el.teacherId).then(teacher => {
            el.teacher = teacher.name;
            this.offers.splice(i, 1, el);
          });
          CourseService.GetCourseById(el.courseId).then(course => {
            el.course = course.name;
            this.offers.splice(i, 1, el);
          });
        }
      });
      CourseService.GetCourses().then(courses => {
        this.courses = courses;
      });
      SchoolService.GetTeachers().then(teachers => {
        this.teachers = teachers;
      });
    }
  },
  methods: {
    deleteItem(item) {
      const index = this.offers.indexOf(item);
      const isConfirmed = confirm("Are you sure you want to delete this item?");
      if (isConfirmed) {
        OfferService.DeleteOffer(item.id).then(() => {
          this.offers.splice(index, 1);
        });
      }
    },
    create() {
      OfferService.CreateOffer({
        courseId: this.selectedCourse,
        teacherId: this.selectedTeacher,
        semester: this.selectedSemester,
        code: this.offerCode
      }).then(() => {
        this.$router.go();
      });
    },
    goToOfferProfile(clickedRow) {
      this.$router.push({
        name: "ViewOffer",
        params: { offerId: clickedRow.id.toString() }
      });
    }
  },

  computed: {
    loggedAs: function() {
      return this.$store.state.loggedAs;
    }
  }
};
</script>

